*{
  font-family:Helvetica sans-serif;
}
body{
  padding: 60px;
  background: linear-gradient(to right, purple,cyan);
  color: white;
}
input{
  color: grey;
  font-size: 1.3rem;
  width: 100%;
}
.card-grid{ 
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  width: 30%;
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img {
  max-height: 170px;
  width: 100%;
  height: 60%;
}